  --background-primary: rgb(25, 32, 36);
  --background-secondary: rgb(43, 51, 56);
  --primary: rgb(255, 179, 92);
  --light: rgb(230, 230, 230);
  --ascent: rgb(255, 179, 92);

  --background-red: rgb(102, 22, 38);
  --background-green: rgba(24, 116, 42, 0.404);
  --red: rgb(212, 43, 77);
  --green: rgb(43, 212, 77);

  --button-background: rgb(25, 32, 36);
  --button-border: rgb(58, 75, 83);

  --priority-high: rgba(253, 107, 136, 0.8);
  --priority-normal: rgba(253, 136, 107, 0.8);
  --priority-low: rgba(191, 247, 100, 0.8);
  --priority-background-high: rgba(253, 107, 136, 0.05);
  --priority-background-normal: rgba(253, 136, 107, 0.05);
  --priority-background-low: rgba(191, 247, 100, 0.05);

  --font-sm: 0.75rem;
  --font-m: 1rem;
  --font-lg: 1.25rem;

  --padding-sm: 6px;
  --padding-m: 12px;
  --padding-lg: 24px
}

*, *::before, *::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

ul,
li {
  list-style-type: none;
}

body {
  position: relative;
  height: 100vh;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  color: var(--light);
}

header,
footer {
  position: sticky;
  text-align: center;
  padding: 10px;
  width: 100%;
  background-color: var(--background-primary);
  color: var(--primary);
  font-size: var(--lg);
}

header {
  top: 0;
  z-index: 2;
  border-bottom: 3px solid var(--ascent);
  font-size: var(--font-lg);
}

footer {
  bottom: 0;
  z-index: 2;
  border-top: 3px solid var(--ascent);
}

footer #github-link{
  color: var(--primary);
}

/* ------- ------- -------
MAIN CONTAINER
------- ------- ------- */

.container {
  padding: 40px;
  min-height: 100%;
  background-color: var(--background-primary);
}


/* ------- ------- -------
BUTTONS
------- ------- ------- */

.button {
  border: 2px solid var(--button-border);
  border-radius: 16px;
  width: 32px;
  height: 32px;
  background-color: var(--button-background);
  display: flex;
  padding: 2px;
  justify-content: start;
  align-items: center;
  cursor: pointer;
  overflow: hidden;
  transition: width 0.2s ease-in-out, background-color 0.2s ease-in-out;
}

.button span {
  min-width: 150px;
  pointer-events: none;
}

.button:hover {
  background-color: var(--background-secondary);
}

.button.add:hover,
.button.delete:hover {
  width: 182px;
}

.button#add-project {
  position: absolute;
  padding-left: 6px;
  top: 10px;
  right: 10px;
  width: 47px;
  height: 47px;
  color: var(--green);
  font-weight: bold;
  border-radius: 20px;
  z-index: 2;
}

.button#add-project:hover {
  width: 182px;
}

.button#add-project img {
  width: auto;
  height: 32px;
}

.button img {
  pointer-events: none;
}

.button.add {
  color: var(--green);
}

.button.delete {
  color: var(--red);
}

.button.add img,
.button.delete img {
  width: 24px;
  height: auto;
}

/* ------- ------- -------
  PROJECT AND TASK MODELS
------- ------- ------- */

.projects {
  display: flex;
  flex-direction: column;
  gap: 20px;
  min-height: 200px; /* Reserve space for content */
}

.project {
  background-color: var(--button-background);
  border: 2px solid var(--button-border);
  border-radius: 20px;
  padding: 10px;
  display: grid;
  gap: 10px;
  align-items: center;
  grid-template-columns: max-content 1fr repeat(2, max-content);
  grid-template-rows: repeat(3, max-content);
  grid-template-areas: 
    "project-completion project-title add-task delete-project"
    "project-completion project-desc project-desc project-desc"
    "project-tasks project-tasks project-tasks project-tasks";
}

.project-title {
  grid-area: project-title;
}

.project-description {
  grid-area: project-desc;
}

.project-completion {
  grid-area: project-completion;
}

.project-tasks {
  grid-area: project-tasks;
  padding: 10px 30px;
  border-top: 2px solid rgba(255, 255, 255, 0.1);
  display: grid;
  gap: 6px;
  min-height: 50px; /* Minimum space for tasks */
}

.project > .button.add {
  grid-area: add-task;
}

.project > .button.delete {
  grid-area: delete-project;
}

.task {
  display: grid;
  gap: 10px;
  grid-template-columns: auto 1fr;
  align-items: center;
}

.task-info {
  border-bottom: 2px solid var(--button-border);
  border-radius: 10px;
  display: grid;
  align-items: center;
  justify-items: start;
  gap: 10px;
  padding: 4px 8px;
  overflow: hidden;
  max-height: 42px;
  grid-template-columns: 2fr 1fr 180px;
  grid-template-rows: 1fr 1fr;
  transition: max-height 0.3s ease-in-out;
  cursor: pointer;
}

.task-info.active {
  gap: 10px;
  max-height: 84px;
}

.task-title {
  grid-area: 1 / 1;
}

.task-due-date {
  grid-area: 1 / 2;
}

.task-description {
  grid-row: 2;
  grid-column: 1 / 4;
}

.task-info > .button.delete {
  grid-area: 1 / 3;
  justify-self: end;
}

/* ------- ------- -------
DIALOGS
------- ------- ------- */

.form-dialog {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: var(--background-secondary);
  border: 2px solid var(--button-border);
  border-radius: 10px;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  color: var(--light);
  width: 90%;
  max-width: 400px;
}

.form-dialog h3 {
  margin-bottom: 20px;
  text-align: center;
}

.form-group {
  margin-bottom: 15px;
}

.form-group label {
  display: block;
  margin-bottom: 5px;
}

.form-group input {
  width: 100%;
  padding: 8px;
  border: 1px solid var(--button-border);
  border-radius: 5px;
  background-color: var(--background-primary);
  color: var(--light);
}

.form-actions {
  display: flex;
  justify-content: space-between;
}

.form-actions button {
  padding: 8px 16px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  background-color: var(--primary);
  color: var(--background-primary);
  font-weight: bold;
  transition: background-color 0.2s ease-in-out;
}

.form-actions button:hover {
  background-color: var(--ascent);
